import React, { useState } from 'react';
import axios from 'axios';
import '../css/MetTestResult.css'
import { url } from '../config';

const MetTestResult = ({ selectedPatient,userInfo}) => {
    const [selectedImage, setSelectedImage] = useState(null);

    //   
    const handleImageChange = (event) => {
        const file = event.target.files[0];
        setSelectedImage(file);//
    };

    //   
    const handleImageUpload = async () => {
        try {
            const formData = new FormData();
            formData.append('image', selectedImage);
            formData.append('testNum', selectedPatient.testNum);
            formData.append('metNum', userInfo.empNum);
            console.log(formData.get('testNum'));


            //     POST  
            const response = await axios.post(`${url}/uploadTestFile`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });
            console.log('  :', response.data);
            //     selectedImage 
            setSelectedImage(null);

        } catch (error) {
            console.error('    :', error);
            //   
            //     
            if (error.response) {
                console.error(' :', error.response.data);
            }
        }
    };
    const patJumin = selectedPatient.patJumin;
    const birthYear = patJumin ? (parseInt(patJumin.substring(0, 2)) + (patJumin[6] <= '2' ? 1900 : 2000)) : null;
    const currentYear = new Date().getFullYear();
    const age = birthYear ? currentYear - birthYear : null;

    return (
        <div className='result-box matmain'>
            <div className='mettitle-box'>
                <img className='meticon' src='./img/MetResult.png' alt='Met Icon'/>
                <span className='mettitle'></span>
                {selectedPatient && (
                        <span className='pat-info'>
                            {selectedPatient.patNum} {selectedPatient.patName} ({selectedPatient.patGender}/{age}) {selectedPatient.patBloodType}
                        </span>
                    )}
            </div>
            <button className='custom-button' onClick={handleImageUpload}></button>
            <label className="custom-button">
                <input type="file" onChange={handleImageChange} />
                
            </label>
            <br/><br/>
            {/*    */}
            {selectedImage && (
                <div>
                    <img src={URL.createObjectURL(selectedImage)} alt="Selected" style={{ maxWidth: '100%', maxHeight: '600px' }} />
                </div>
            )}

            
        </div>
    );
};

export default MetTestResult;
