import React, { useState } from 'react';
import "../css/NurDailyPrescription.css";
import { useAtomValue } from 'jotai';
import { usernameAtom } from '../config/Atom';
import { url } from '../config';
import axios from 'axios';

const PrescModal = ({ prescription, closeModal, buttonNum }) => {
    const username = useAtomValue(usernameAtom);
    const [isChecked1, setIsChecked1] = useState(false);
    const [isChecked2, setIsChecked2] = useState(false);
    const [isChecked3, setIsChecked3] = useState(false);
    const [isChecked4, setIsChecked4] = useState(false);
    const [diaryStatus, setDiaryStatus] = useState('');

    //    
    const handleClick = (status) => {
        //      
        switch (status) {
            case 'none':
                setIsChecked1(true);
                setIsChecked2(false);
                setIsChecked3(false);
                setIsChecked4(false);
                break;
            case 'success':
                setIsChecked1(false);
                setIsChecked2(true);
                setIsChecked3(false);
                setIsChecked4(false);
                break;
            case 'returnO':
                setIsChecked1(false);
                setIsChecked2(false);
                setIsChecked3(true);
                setIsChecked4(false);
                break;
            case 'returnX':
                setIsChecked1(false);
                setIsChecked2(false);
                setIsChecked3(false);
                setIsChecked4(true);
                break;
            default:
                break;
        }
        // diaryStatus  
        setDiaryStatus(status);
    };

    //     
    const updatePresc = () => {
        const date = new Date();
        const isoString = date.toISOString();
        const hoursAndMinutes = isoString.substring(11, 16);

        const data = {
            patNum: prescription.patNum,
            prescriptionNum: prescription.prescriptionNum,
            buttonNum: buttonNum,
            nurNum: username,
            diaryStatus: diaryStatus,
            diaryTime: hoursAndMinutes
        };

        axios.post(`${url}/updatePrescriptionStatus`, data)
            .then(res => {
                console.log(res);
                closeModal();
            })
            .catch(err => {
                console.log(err);
            });
    };

    return (
        <div className="PrescModalbox">
            <br />
            <div className="boxHeader">
                <h3 id="boxHeader" style={{ margin: "0 auto" }}>  </h3>
            </div>
            <br />
            <div className="prescContent">
                {/*  */}
                <label className={isChecked1 ? 'labelL radio-checked' : 'labelL'} style={{ backgroundColor: "lightgray" }} htmlFor="presX1">
                    <input type="radio" id="presX1" style={{ height: "20px", display: "none", zIndex: "9999" }} onChange={() => handleClick('none')} /> 
                </label>

                {/*   */}
                <label className={isChecked2 ? 'labelL radio-checked' : 'labelL'} style={{ backgroundColor: "#F7CE7E" }} htmlFor="presX2">
                    <input type="radio" id="presX2" style={{ height: "20px", display: "none", zIndex: "9999" }} onChange={() => handleClick('success')} />  
                </label>

                {/*     */}
                <label className={isChecked3 ? 'labelL radio-checked' : 'labelL'} style={{ backgroundColor: "#609E66" }} htmlFor="presX3">
                    <input type="radio" id="presX3" style={{ height: "20px", display: "none", zIndex: "9999" }} onChange={() => handleClick('returnO')} />  X -  
                </label>

                {/*     */}
                <label className={isChecked4 ? 'labelL radio-checked' : 'labelL'} style={{ backgroundColor: "red", marginBottom: "15px" }} htmlFor="presX4">
                    <input type="radio" id="presX4" style={{ height: "20px", display: "none", zIndex: "9999" }} onChange={() => handleClick('returnX')} />  X -  
                </label>
            </div>
            <button id="memoButton" style={{ margin: "0" }} onClick={updatePresc}></button>
        </div>
    );
};

export default PrescModal;